@import '~@styles/_variables';

.container {
    color: #fff;
    position: absolute;
    text-align: center;
    width: 200px;

    bottom: 0;
    left: -57vh;
    top: 0;

    transform: rotate(90deg);

    .footerLink {
        background-color: rgba(0, 0, 0, 0.5);
        border-radius: 5px;
        font-size: 20px;
        padding: 5px;
        z-index: 100;

        transition: color .3s;
        -webkit-transition: color .3s;
        -moz-transition: color .3s;
        -ms-transition: color .3s;
        -o-transition: color .3s;
    
        &:hover {
            color: $hover-color;
        }

        svg {
            font-size: 24px;

            @media (min-width: $breakpoint-small) {
                font-size: 32px
            }
        }

        @media (min-width: $breakpoint-small) {
            font-size: 32px;
        }

    }

    @media (min-width: $breakpoint-small) {
        bottom: 10px;
        left: 0;
        right: 0;
        top: auto;

        transform: rotate(0);
        width: 100%;
    }
}